<template>
	<view class="content">
		<k-scroll-view ref="k-scroll-view" :refreshType="refreshType" :refreshTip="refreshTip" :loadTip="loadTip"
		 :loadingTip="loadingTip" :emptyTip="emptyTip" :touchHeight="touchHeight" :height="height" :bottom="bottom"
		 :autoPullUp="autoPullUp" @onPullDown="handlePullDown" @onPullUp="handleLoadMore">
			<uni-card class="ui-bg" :title="item.comName" v-for="(item,index) in list" :extra="item.createtime | renderTime" :key="item.id">
				<view :class="{'bgdb':item.metertype=='电表','bgsb':item.metertype=='水表','bgrqb':item.metertype=='燃气表'}">
					<view class="ui-item"><span class="ui-itemTitle">业主:</span> <span class="ui-span">{{item.clientName}}</span></view>
					<view class="ui-item"><span class="ui-itemTitle">单元:</span> <span class="ui-span">{{item.buildName}}</span></view>
					<view class="ui-item"><span class="ui-itemTitle">楼层:</span> <span class="ui-span">{{item.louName}}</span></view>
					<view class="ui-item"><span class="ui-itemTitle">业主电话:</span> <span class="ui-span">{{item.clientTel}}</span></view>
					<view class="ui-item"><span class="ui-itemTitle">表类型:</span> <span class="ui-span">{{item.metertype}}</span></view>
					<view class="ui-item"><span class="ui-itemTitle">操作员:</span> <span class="ui-span">{{item.propertyName}}</span></view>
					<view class="ui-item"><span class="ui-itemTitle">操作电话:</span> <span class="ui-span">{{item.propertyTel}}</span></view>
					<view v-if="item.remark" class="ui-item">备注: <span class="ui-span">{{item.remark}}</span></view>
					<view class="ui-item ui-display">
						<image @click="preview(itemImg)" v-if="itemImg" v-for="(itemImg,index) in item.imgList" :key="index" :src="host+''+itemImg" mode="aspectFill "></image>
					</view>
				</view>
			</uni-card>
		</k-scroll-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list:[],
				toatlNum:5,
				currPage:1
			}
		},
		filters:{
			 renderTime(date) {
					console.log(typeof date)
					if(date){
						let str = date.replace(/-/g, '/').replace('T', ' ').replace('.000+0000', '')
						console.log(new Date(str).toString())
						 var dates = new Date(new Date(str).toString())
						 var y = dates.getFullYear()
						       var m = dates.getMonth() + 1
						       m = m < 10 ? ('0' + m) : m
						       var d = dates.getDate()
						       d = d < 10 ? ('0' + d) : d
									 var h = dates.getHours() + 8
									 h = h < 10 ? ('0' + h) : h
									 var min = dates.getMinutes();
									 min = min < 10 ? ('0' + min) :min
									 var s = dates.getSeconds();
									 s = s < 10 ? ('0' + s) : s;
						       const time = y + '-' + m + '-' + d + '  '+h+":" + min + ':' + s
						       return time
						}
					}
			},
		onLoad(options) {
			this.getList(options.id)
		},
		methods:{
			handlePullDown(stopLoad){
				this.currPage=1,
				this.getList()
				
				stopLoad ? stopLoad({
					isEnd: false
				}) : '';
			},
			handleLoadMore(stopLoad){
				let obj={
					id:uni.getStorageSync("clientmessageid"),
					toatlNum:this.toatlNum,
					currPage:this.currPage
				}
				this.$api.getMeterListByID(obj).then(res=>{
					console.log(res)
					if (listlast.length == 0) {
						stopLoad ? stopLoad({
							isEnd: true
						}) : '';
						return
					}
					uni.hideLoading()
					this.list = this.list.concat(res.data)
				})
				
			},
			getList(){
				let obj={
					id:uni.getStorageSync("clientmessageid"),
					toatlNum:this.toatlNum,
					currPage:this.currPage
				}
				this.$api.getMeterListByID(obj).then(res=>{
					uni.hideLoading()
					this.list = res.data
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.bgdb{
		background-image: url(data:image/jpeg;base64,);
		background-size: 300rpx 300rpx ;
		background-repeat: no-repeat;
		background-position: top right;
	}
	.bgrqb{
		background-image: url(data:image/jpeg;base64,);
		background-size: 300rpx 300rpx ;
		background-repeat: no-repeat;
		background-position: top right;
	}
	.bgsb{
		background-image:url(data:image/jpeg;base64,);
		background-size: 300rpx 300rpx ;
		background-repeat: no-repeat;
		background-position:top right;
	}
.ui-item{
		padding: 10rpx 0;
		.ui-itemTitle{
			display: inline-block;
			width: 150rpx;
		}
	}
	.content{
		padding-bottom: 10rpx;
	}
</style>
